
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

li {
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
  font-style: normal;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

.container {
  width: 320px;
  padding-left: 16px;
  padding-right: 16px;
  margin: 0 auto;
  outline: solid 1px blue;
}


.box-news {
  margin-top: 20px;
}

.box-news__item {
  position: relative;
  height: 630px;
  margin-bottom: 28px;

  outline: solid 1px red;
}

.box-news__section {
  position: absolute;
  top: 40px;
  left: 0;
  display: inline-block;
  min-width: 112px;
  padding: 4px 8px;
  font-family: 'Manrope';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  text-align: center;
  background: rgba(68, 64, 247, 0.7);
  border-radius: 0px 4px 4px 0px;
  color: #ffffff;
}

.box-news__reading {
  position: absolute;
  top: 14px;
  right: 14px;
  font-family: 'Manrope';
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 1,36;
  color: #00dd73;
}

.box-news__thumb {
  border-radius: 4px;
  width: 100%;
  height: 395px;
  overflow: hidden;
  margin-bottom: 20px;
}

.box-news__img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.box-news__wrap-title {
  display: flex;
  margin-bottom: 20px;
}

.box-news__titel {
  height: 99px;
  font-family: 'Manrope';
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 1.38;
  letter-spacing: -0.01em;
}

.box-news__text {
  height: 57px;
  margin-bottom: 20px;
  font-family: 'Manrope';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.36;
}

.box-news__wrap {
  display: flex;
  justify-content: space-between;
}

.box-news__data {
  font-family: 'Manrope';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.36;
  color: #a8a8a8;
}

.box-news__link {
  font-family: 'Manrope';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.36;
  color: #4440f7;
}
